@import '../../stylesheet/const';

.sport_menu {
   background-color: rgb(239, 255, 96);  
   .top_menu {
      display: flex;
      justify-content: space-between;
      align-items: center;
   
      .sport_header {
         margin: 0;
         padding: 10px 0;
         font-size: 50px;
         color: rgb(92, 92, 92);
         font-style: italic;
      }
   
      .img {
         width: 150px;
         padding: 15px 0;
      }
   }
   
   .bottom_menu {
      background-color: rgb(129, 39, 39);
      
      .nav {
         display: flex;
         justify-content: space-between;
         align-items: center;
         position: relative;
   
      }
   
      .menu_list {
         display: flex;
         
         &_item {
            font-size: $menu_fs;
            font-weight: 500;
            padding: 10px;
            position: relative;
            cursor: pointer;
            color: black;
   
            &::before {
               content: '';
               position: absolute;
               width: 1px;
               height: 25px;
               background-color: black;
               left: 0;
               top: 50%;
               transform: translateY(-50%);
            }
   
            &:hover::after, &.active::after {
               content: '';
               position: absolute;
               width: 70%;
               height: 4px;
               bottom: 0;
               left: 50%;
               transform: translateX(-50%);
               background-color: rgb(0, 0, 0);
            }
         }
      }
   
      .burger {
         cursor: pointer;
         position: relative;
         width: 30px;
         height: 20px;
         
         & span {
            position: absolute;
            background-color: black;
            left: 0;
            width: 100%;
            height: 2px;
            top: 9px;
            transition: all 0.3s ease-in-out;
         }
   
         &.active span {
            transform: scale(0);
         } 
   
         &::before, &::after {
            content: '';
            background-color: black;
            position: absolute;
            width: 100%;
            height: 2px;
            left: 0;
            transition: all 0.3s ease-in-out;
         }
   
         &::before {
            top: 0;
         }
   
         &.active::before  {
            transform: rotate(45deg);
            top: 9px;
         }
   
         &.active::after {
            transform: rotate(-45deg);
            bottom: 9px;
         }
   
         &::after {
            bottom: 0;
         }
      }
   }
}
